import { Injector, NgModule } from "@angular/core";
import { createCustomElement } from "@angular/elements";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { BrowserModule } from "@angular/platform-browser";
import { DynaformModule } from "dynaform";
import { ElementZoneStrategyFactory } from "elements-zone-strategy";
import { ColorPickerModule } from "ngx-color-picker";
import { AppComponent } from "./app.component";
import { CircleComponent } from "./components/circle/circle.component";
import { EllipseComponent } from "./components/ellipse/ellipse.component";
import { GroupComponent } from "./components/group/group.component";
import { ImageComponent } from "./components/image/image.component";
import { LineComponent } from "./components/line/line.component";
import { PathComponent } from "./components/path/path.component";
import { PolyLineComponent } from "./components/polyline/polyline.component";
import { RectangleComponent } from "./components/rectangle/rectangle.component";
import { ShapeComponent } from "./components/shape/shape.component";
import { SquareComponent } from "./components/square/square.component";
import { TextComponent } from "./components/text/text.component";
import { DynamicSvgDirective } from "./directives/dynamic-svg.directive";
import { ShapeService } from "./service/shape.service";

@NgModule({
  declarations: [
    AppComponent,
    LineComponent,
    CircleComponent,
    RectangleComponent,
    DynamicSvgDirective,
    ShapeComponent,
    SquareComponent,
    EllipseComponent,
    TextComponent,
    GroupComponent,
    ImageComponent,
    PolyLineComponent,
    PathComponent,
  ],
  entryComponents: [
    ShapeComponent,
    LineComponent,
    CircleComponent,
    RectangleComponent,
    SquareComponent,
    EllipseComponent,
    TextComponent,
    GroupComponent,
    ImageComponent,
    PolyLineComponent,
    PathComponent,
  ],
  imports: [
    BrowserModule,
    ColorPickerModule,
    FormsModule,
    DynaformModule,
    ReactiveFormsModule,
  ],
  providers: [ShapeService],
  bootstrap: [AppComponent],
})
export class AppModule {
  injector: Injector;
  constructor(injector: Injector) {
    this.injector = injector;
    const strategyFactory = new ElementZoneStrategyFactory(
      AppComponent,
      this.injector
    );
    const webComponent = createCustomElement(AppComponent, {
      injector,
      strategyFactory,
    });
    customElements.define("svg-drawing-tool", webComponent);
  }

  ngDoBootstrap() {}
}
